import React from "react";
import Cart from './Cart';
import Time from './Time';

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			val:0,
			clas : 'cart',
			clas1 : 'cart',
		
		}
		this.Clic = this.Clic.bind(this);
		// this.onClick = this.onClick.bind(this);
	}
	Clic() {
		//  a?this.setState({ clas:'done cart' }) :this.setState({ clas:'not cart' });
		if(Math.random()>0.5){
			this.setState({ clas1:'cart' });
			this.setState({ clas:'cart' });
			this.setState({ clas:'done cart' });
			this.setState({val: this.state.val + 1});
		} else {
			this.setState({ clas:'not cart' });
			this.setState({val: 0});

		}
		setTimeout(()=>this.setState({ clas:'cart' }),1000);
		
	
	}
	Clic1() {
		//  a?this.setState({ clas:'done cart' }) :this.setState({ clas:'not cart' });
		if(Math.random()>0.5){
			this.setState({ clas:'cart' });
			this.setState({ clas1:'cart' });
			this.setState({ clas1:'done cart' });
			this.setState({val: this.state.val + 1});
		} else {
			this.setState({ clas1:'not cart' });
			this.setState({val: 0});
		}
		setTimeout(()=>this.setState({ clas1:'cart' }),1000);
	
	}
	render() { 
		let btnn;
		if(this.state.val===3) {
			setTimeout(()=>this.setState({val: 0}),2000);
			
			btnn= <div>Вы победили!!!</div>
		}
// 		let random=Math.random();
// 		let a;
// 		if(random>0.5){
// a=true
// 		}else a= false;
// 		let b =!a;
		return(
		<div className='wrap'>
			<Time/>
			<div className='num'>Сыграем до 3х</div>
			<div className='num'>Угаданно: {this.state.val}</div>
			{btnn}
			<div className='pole' >
				
				<Cart clas={this.state.clas} cart='1' onClick={() => this.Clic('1')}/>
				<Cart clas={this.state.clas1} cart='2' onClick={() => this.Clic1('2')}/>
				{/* onClick={()=>this.setState({val:this.state.val+1})} */}

</div>
		</div> 
	
	);
}
}

export default App;
